// 导入自定义组件
import trailerStarts from "../../components/trailerStars.vue"


//在页面创建的时候 创建一个临时动画对象
var animation = uni.createAnimation();


export default {
	data() {
		return {
			carouselList: [],
			hotSuperHeroList: [],
			hotTrailerList: [],
			guessULikeList:[],
			animationData: {},
			animationDataArr:[{}, {}, {}, {}, {}],
			indicatoractivecolor: "rgb(255, 255, 255, 0.5)"
		}
	},

	onLoad() {
		var me = this;
		//请求轮播图数据
		uni.request({
			url: me.serverURL + '/index/carousel/list?&qq=843002185',
			method: "POST",
			success: (res) => {
				if (res.data.status == 200) {
					var carouselList = res.data.data;
					me.carouselList = carouselList;
				}
			}
		});

		//请求热门超英
		uni.request({
			url: me.serverURL + '/index/movie/hot?&qq=843002185&type=superhero',
			method: "POST",
			success: (res) => {
				if (res.data.status == 200) {
					me.hotSuperHeroList = res.data.data;
				}
			}
		});
		//请求热门预告
		uni.request({
			url: me.serverURL + '/index/movie/hot?&qq=843002185&type=superhero',
			method: "POST",
			success: (res) => {
				if (res.data.status == 200) {
					me.hotTrailerList = res.data.data;
				}
			}
		});			
		me.refresh();
	},
	onUnload() {
		//页面卸载的时候 清除数据
		me.animationData = {};
		me.animationDataArr = [{}, {}, {}, {}, {}];
	},
	
	//下拉刷新
	onPullDownRefresh() {
		me.refresh();
	},
	onShow() {

	},
	onHide() {
		if (this.videoContext) {
			this.videoContext.pause();
			console.log("离开页面 停止播放");
		}
	},
	methods: {
		refresh(){
			var me = this;
			//页面显示加载
			uni.showLoading({
				mask:true,//是否屏蔽页面点击
			});
			
			//navigationbar 显示加载
			uni.showNavigationBarLoading();
			
			//请求猜你喜欢
			uni.request({
				url:me.serverURL + '/index/guessULike?qq=843002185',
				method:"POST",
				success: (res) => {
					if (res.data.status == 200) {
						me.guessULikeList = res.data.data;
					}
				},
				complete() {
					//隐藏刷新
					uni.hideNavigationBarLoading();
					uni.hideLoading();
					//加载完成后结束下拉刷新
					uni.stopPullDownRefresh();
				}
			})
		},
		pariseMe(e) {
			var me = this;

			var gIndex = e.currentTarget.dataset.gindex;
			
			//构建动画数据，并通过step来表示这组动画的完成
			animation.translateY(-60).opacity(1).step({
				duration: 400
			});
			me.animationData = animation;
			me.animationDataArr[gIndex] = animation.export();
			setTimeout(function() {
				animation.translateY(0).opacity(0).step({
					duration: 0
				});
				me.animationData = animation;
				me.animationDataArr[gIndex] = animation.export();
			}.bind(me), 600); // 5秒

			
		},
		meIsPlay(e) {
			var me = this;
			var trailerId = "";
			if (e) {
				trailerId = e.currentTarget.dataset.playindex;
				me.videoContext = uni.createVideoContext(trailerId);
			}
			var hotTrailerList = me.hotTrailerList;
			
			for (var i = 0; i < hotTrailerList.length; i++) {
				var tmpID = hotTrailerList[i].id;
				if (tmpID != trailerId) {
					uni.createVideoContext(tmpID).pause();
				}
			}
		}
	},
	components: {
		trailerStarts
	}
}
